*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.roadMapSec {
  background-color: #814780;
  height: 150vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.road-map {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  background-image: url('../assets/background.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
}
.title {
  color: white;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}
.branch-1,
.branch-2,
.branch-3,
.branch-4{
  background-color: antiquewhite;
  text-align: left;
  padding-left: 12px;
  border: solid 2px;
  border-radius: 6px;
  max-width: 350px;
}

.branch-container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 750px;
  max-width: 800px;
  font-weight: bold;
  padding-top: 100px;
}
.branch-1 {
  position: relative;
  font-size: 10px;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  width: 260px;
  padding-right: 40px;
  padding-top: 8px;
  margin-top: -100px;  
}
.branch-1 ol li{
    margin-left: -17px;
    padding-left: 1rem;
    padding-top: 10px;
}
.branch-2 {
  position: relative;
  font-size: 10px;
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  width: 250px;
  padding-top: 8px;
  margin-top: -50px;
  
}
.branch-2 ol li {
  padding-top: 10px;
  padding-left: 1rem;
  margin-left: -17px;
}
.branch-3 {
  position: relative;
  font-size: 10px;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  width: 260px;
  padding-right: 40px;
  padding-top: 8px;
  margin-top: -50px;
}
.branch-3 ol li{
  padding-top: 10px;
  padding-left: 1rem;
  margin-left: -17px;
}
.branch-4 {
  position: relative;
  font-size: 10px;
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  width: 250px;
  padding-top: 8px;
  margin-top: -50px;
}
.branch-4 ol li {
  padding-top: 10px;
  padding-left: 1rem;
  margin-left: -17px;
}

@media (max-width: 918px) {
  .roadMapSec {
    height: max-content;
    padding: 0 20px;
  }

  .branch-container{
    min-width: 50px;
    min-width: 75px;
    padding-top: 0;
    row-gap: 25px;
  }

  .branch-1, .branch-2, .branch-3, .branch-4 {
    width: 250px;
    margin-top: 0;  
  }
}